import React from 'react';
import styled from 'styled-components';
import Collapse, { Panel } from 'rc-collapse';
import 'rc-collapse/assets/index.css';

//Components
import Heading from '../../Heading';
import Paragraph from '../../Paragraph';
import Icon from '../../Icon';

const HeaderCollapse = props => {
  return (
    <StyleHeaderCollapse>
      <Icon icon="chevron-down" />
      <Icon icon="chevron-right" />
      {props.title}
    </StyleHeaderCollapse>
  )
}

const PublicOfferContractSection = props => {
  return (
    <Wrap>
      <SectionHeadingWrap>
        <Heading size="2.3">
          Договор публичной оферты на оказание информационных услуг
        </Heading>
        <Paragraph size="0.9">
          Общество с ограниченной ответственностью «Новая Медицина» (далее «Исполнитель»), в лице генерального директора Зайдуллина Р.Г., действующего на основании Устава, предлагает физическим лицам (далее «Заказчик») заключить Договор на оказание платных медицинских услуг (далее «Договор») на следующих условиях:
        </Paragraph>
      </SectionHeadingWrap>
      <AccordionWrap>
        <Collapse defaultActiveKey={"0"} accordion={false}>
          <Panel
            showArrow={false}
            header={<HeaderCollapse title="1. Термины и определения" />}
            headerClass="my-header-class">
            В целях настоящей Оферты нижеприведенные термины используются в следующих значениях:

Оферта – предложение Исполнителя, адресованное физическим лицам в соответствии со статьей 437 Гражданского кодекса Российской Федерации, заключить с ним Договор на оказание платных медицинских услуг на условиях, содержащихся в настоящей Оферте, опубликованной в сети Интернет по адресу: http://docplus.ru/legal/.

Акцепт Оферты – полное и безоговорочное принятие Оферты путем осуществления действий, указанных в п. 2.1. Оферты.

Заказчик – физическое лицо, приобретающее и/или получающее платные медицинские услуги в соответствии с Договором.

Исполнитель – ООО «Новая Медицина», оказывающее платные медицинские услуги на основании выданной лицензии № ЛО-77-01-014178 от 19 апреля 2017 года (далее «Лицензия»)

Прейскурант – действующий перечень медицинских услуг Исполнителя с ценами, публикуемый по адресу: www.doconcall.ru/legal/, являющийся неотъемлемой частью настоящей Оферты.
          </Panel>
          <Panel
            showArrow={false}
            header={<HeaderCollapse title="Сколько стоит подбор специалиста по уходу?" />}
            headerClass="my-header-class">
            В удобнное время к вам приедет наша пробная сиделка, объяснит вам как работают внуки, и поможет составить индивидуальную программу специально под вам и ваших близких
          </Panel>
          <Panel
            showArrow={false}
            header={<HeaderCollapse title="Сколько стоит подбор специалиста по уходу?" />}
            headerClass="my-header-class">
            В удобнное время к вам приедет наша пробная сиделка, объяснит вам как работают внуки, и поможет составить индивидуальную программу специально под вам и ваших близких
          </Panel>
        </Collapse>
      </AccordionWrap>
    </Wrap>
  )
}

const AccordionWrap = styled.div`
  margin-top: 30px;
  width: 50%;
  margin: auto;
  margin-top: 20px;
  text-align: left;
  .rc-collapse {
    border-left: 0px solid #fff !important;
    border-right: 0px solid #fff !important; 
    border-color: #e2e1d9;
    background: transparent;
    .rc-collapse-item {
      padding: 8px 0;
    }
  }
  .rc-collapse-header {
    background: transparent;
    font-family: "Alegreya Sans";
    font-weight: 500;
    color: #222 !important;
    font-size: 1.3em;
    outline: none;
    line-height: 20px !important;
    height: auto !important;
  }
  .rc-collapse-content {
    color: #222 !important;
    font-size: 1em;
    font-weight: 400;
    font-family: "Alegreya Sans";
    margin-left: 46px;
    background: transparent;
  }
  .rc-collapse-item-active {
    .mdi-chevron-right {
      display: none;
    }
    .mdi-chevron-down {
      display: block !important;
    }
  }
`

const StyleHeaderCollapse = styled.div`
  display: flex;
  align-items: center;
  .mdi-chevron-down {
    display: none !important;
  }
  i {
    color: #ff6053;
    font-size: 1.3em;
    position: relative;
    top: 3px;
  }
`

const SectionHeadingWrap = styled.div`
  max-width: 630px;
  text-align: center;
  margin: auto;
  h1 {
    line-height: 43px;
  }
  p {
    max-width: 550px;
    margin: auto;
    margin-top: 20px;
    
  }
`

const Wrap = styled.div`
  padding-top: 32px;
  padding-bottom: 37px;
  background-color: #edebe3;
`

export default PublicOfferContractSection


