import React, { Component } from 'react';
import styled from 'styled-components';
import { Link } from 'react-router-dom';
import Icon from '../Icon';
import Logo from '../../img/logo.svg'

export default class Header extends Component {
  state = {
    openMobileMenu: false,
  }

  onMobileMenu = () => this.setState({ openMobileMenu: !this.state.openMobileMenu });

  render() {
    return [
      <HeaderContainer>
        <HeaderTopWrap>
          <HeaderPhoneNumber>
            +7 (495) 120-19-90
          </HeaderPhoneNumber>
          <HeaderTopMenu>
            <HeaderTopMenuLink to="/">
              Организация ухода
            </HeaderTopMenuLink>
            <HeaderTopMenuLink to="/">
              Академия Близких
            </HeaderTopMenuLink>
            <HeaderTopMenuLink to="/">
              Журнал
            </HeaderTopMenuLink>
            <HeaderTopMenuButtonsWrap>
              <HeaderTopMenuButton red={true} to="/">
                Найти помощника
              </HeaderTopMenuButton>
              <HeaderTopMenuButton to="/">
                <Icon icon="account" />
                Войти
                </HeaderTopMenuButton>
            </HeaderTopMenuButtonsWrap>
          </HeaderTopMenu>
        </HeaderTopWrap>
        <HeaderMenuBottomWrap>
          <HeaderBottomLogoWrap to="/">
            <img alt="Logo" src={Logo} />
            <h1>
              близкие
              </h1>
          </HeaderBottomLogoWrap>
          <HeaderBottonMenuWrap>
            <HeaderMenuBottomLin to="/uslugi">
              Услуги
            </HeaderMenuBottomLin>
            <HeaderMenuBottomLin to="/specialists">
              Специалисты
            </HeaderMenuBottomLin>
            <HeaderMenuBottomLin to="/security">
              Безопасность
            </HeaderMenuBottomLin>
            <HeaderMenuBottomLin to="/">
              Служба помощи
            </HeaderMenuBottomLin>
          </HeaderBottonMenuWrap>
        </HeaderMenuBottomWrap>
      </HeaderContainer>,
      <MobileHeaderWrap>
        <MobileHeading>
          близкие
        </MobileHeading>
        <MobileBurgerBtn onClick={this.onMobileMenu}>
          <Icon icon="menu" />
        </MobileBurgerBtn>
      </MobileHeaderWrap>,
      <MobileMenu open={this.state.openMobileMenu}>
        <div className="menu_wrap">
          <HeaderMenuBottomLin to="/uslugi">
            Услуги
            </HeaderMenuBottomLin>
          <HeaderMenuBottomLin to="/specialists">
            Специалисты
            </HeaderMenuBottomLin>
          <HeaderMenuBottomLin to="/security">
            Безопасность
            </HeaderMenuBottomLin>
          <HeaderMenuBottomLin to="/">
            Служба помощи
          </HeaderMenuBottomLin>
          <HeaderMenuBottomLin to="/">
            Профиль
          </HeaderMenuBottomLin>
          <HeaderPhoneNumber>
            +7 (495) 120-19-90
          </HeaderPhoneNumber>
          <HeaderTopMenuButton red={true} to="/">
            Найти помощника
          </HeaderTopMenuButton>
        </div>
      </MobileMenu>
    ]
  }
}

const MobileMenu = styled.div`
  display: ${props => props.open ? 'block' : 'none'};
  min-height: 100vh;
  background-color: #222;
  border-top: 1px solid #444;
  position: fixed;
  top: 40px;
  width: 100%;
  z-index: 10000;
  .menu_wrap {
    width: 90%;
    margin: 0 auto;
    margin-top: 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
    a {
      font-size: 2.5em;
      margin: 10px 0; 
    }
  }

  @media(min-width: 768px) {
    display: none;
  }
`

const MobileBurgerBtn = styled.div`
   i {
     color: #fff;
     font-size: 2em;
     cursor: pointer;
   }
`

const MobileHeading = styled.div`
  font-size: 2em;
  color: #fff;
  
`

const MobileHeaderWrap = styled.div`
  display: none;
  background-color: #222222;
  min-height: 40px;
  padding-left: 2.5%;
  padding-right: 2.5%;
  justify-content: space-between;
  align-items: center;
  position: fixed;
  width: 100%;
  box-sizing: border-box;
  top: 0;
  z-index: 10000;
  margin-bottom: 80px;
  @media(max-width: 768px) {
    display: flex;
  }
`

const HeaderMenuBottomLin = styled(Link)`
  font-size: 1.3em;
  color: #222222;
  font-weight: 500;
  text-decoration: none;
  transition: ease 0.5s all;
  font-family: "Alegreya Sans";
  &:hover, .active {
    color: #ff6053;
  }
  @media(max-width: 768px) {
    color: #fff;
  }
`

const HeaderBottonMenuWrap = styled.div`
  display: flex;
  align-items: center;
  justify-content: space-around;
  width: 60%;

`

const HeaderBottomLogoWrap = styled(Link)`
  width: 25%;
  padding-right: 15%;
  display: flex;
  align-items: center;
  text-decoration: none;
  img {
    width: 40px;
    height: 40px;
  }
  h1 {
    margin: 0;
    margin-left: 20px;
    color: #ff6053;
    font-size: 2.5em;
    font-weight: 400;
  }
`

const HeaderMenuBottomWrap = styled.div`
  display: flex;
  align-items: center;
  padding-top: 28px;
  padding-bottom: 28px;
  width: 90%;
  margin: 0 auto;
`

const HeaderTopMenuButtonsWrap = styled.div`
  min-height: 40px;
  box-sizing: border-box;
  display: flex;
`

const HeaderTopMenuButton = styled(Link)`
  box-sizing: border-box;
  color: #fff;
  font-size: 0.8em;
  background-color: ${props => props.red ? "#ff6053" : 'transparent'};
  text-decoration: none;
  padding-top: 10px;
  padding-left: 20px;
  padding-right: 20px;
  height: 40px;
  transition: ease 0.5s all;
  font-family: "Alegreya Sans";
  font-weight: 500;
  &:hover {
    background-color: ${props => props.red ? "#c74c42" : 'transparent'};
  }
  i {
    font-size: 1em;
    margin-right: 2px;
  }
  @media(max-width: 768px) {
    font-size: 1em !important;
    border-radius: 4px;
    box-sizing: content-box;
    height: auto;
    padding: 15px 50px;
  }
`

const HeaderTopMenuLink = styled(Link)`
  color: #fff;
  font-size: 0.8em;
  text-decoration: none;
  transition: ease 0.5s all;
  box-sizing: border-box;
  border-bottom: 4px solid transparent;
  font-family: "Alegreya Sans";
  min-height: 40px;
  max-height: 40px;
  padding-top: 10px;
  &:hover, &.active {
    color: #ff6053;
    border-bottom: 4px solid #ff6053;
  }
`

const HeaderTopMenu = styled.div`
  width: 50%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  @media(max-width: 1256px) {
    width: 80%;
  }
`

const HeaderTopWrap = styled.div`
  width: 100%;
  max-width: 100%;
  background-color: #222222;
  min-height: 40px;
  max-height: 40px;
  padding-left: 5%;
  box-sizing: border-box;
  display: flex;
  justify-content: space-between;
  align-items: center;
`

const HeaderContainer = styled.div`
  width: 100%;
  @media(max-width: 768px) {
    display: none;
  }
`

const HeaderPhoneNumber = styled.p`
  color: #fff;
  font-size: 1em;
  width: 45%;
  font-family: "Alegreya Sans";
  font-weight: 500;
  @media(max-width: 1256px) {
    width: 15%;
  }
  @media(max-width: 768px) {
    border: 1px solid #444;
    border-radius: 4px;
    font-size: 1em !important;
    box-sizing: content-box;
    height: auto;
    padding: 15px 50px;
    width: auto;
    margin-top: 30px !important;
  }
`


